import { graphql, useStaticQuery } from 'gatsby'
import React from 'react'
const url = require('url')

//import useSiteMetadata from '../hooks/useSiteMetadata'

const OgSiteTemplate = () => {
    //const { title, author, description, siteUrl } = useSiteMetadata()
    
    const query = graphql`
    {
        site {
            siteMetadata {
                title, author, siteUrl
            }
        }
    }
    `
    const { site } = useStaticQuery(query)
    //console.log("site: ", site)
    const { title, author, siteUrl } = site.siteMetadata
    const domain = url.parse(siteUrl).hostname
    //console.log("domain", domain)
    return (
        <div  style={{margin: "3rem"}}>
             <style global>{`
                body {
                    background-color: #227;
                    color: white;
                }
            `
            }
            </style>
            <h1>{title}</h1>
            <h3>{author}</h3>
            <h3>{domain}</h3>

        </div>
    )
}

export default OgSiteTemplate
